.abc-analysis {
  position: relative;
  padding: 1.5rem;
  background-color: #f9fafb;
  min-height: 100vh;
}
.abc-analysis .loading-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 4rem 2rem;
  text-align: center;
}
.abc-analysis .loading-state .loading-spinner {
  width: 3rem;
  height: 3rem;
  border: 3px solid #f3f4f6;
  border-top: 3px solid #3b82f6;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-bottom: 1rem;
}
.abc-analysis .loading-state p {
  color: #6b7280;
  font-size: 1.125rem;
}
@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.abc-analysis .error-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 4rem 2rem;
  text-align: center;
}
.abc-analysis .error-state .error-title {
  color: #ef4444;
  font-size: 1.25rem;
  font-weight: 600;
  margin: 1rem 0;
}
.abc-analysis .error-state .btn {
  margin-top: 1rem;
}
.abc-analysis .abc-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 2rem;
}
.abc-analysis .abc-header .header-left .main-title {
  font-size: 1.875rem;
  font-weight: 700;
  color: #111827;
  margin: 0 0 0.5rem 0;
}
.abc-analysis .abc-header .header-left .subtitle {
  font-size: 1rem;
  color: #6b7280;
  margin: 0;
}
.abc-analysis .abc-header .header-right {
  display: flex;
  gap: 0.75rem;
}
.abc-analysis .abc-header .header-right .btn {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.75rem 1.5rem;
  border-radius: 0.5rem;
  font-weight: 500;
  transition: all 0.2s;
  cursor: pointer;
  border: none;
}
.abc-analysis .abc-header .header-right .btn.btn-secondary {
  background-color: white;
  color: #374151;
  border: 1px solid #d1d5db;
}
.abc-analysis .abc-header .header-right .btn.btn-secondary:hover {
  background-color: #f9fafb;
  border-color: #9ca3af;
}
.abc-analysis .abc-header .header-right .export-btn {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.75rem 1.5rem;
  background-color: #3b82f6;
  color: white;
  border: none;
  border-radius: 0.5rem;
  font-size: 0.875rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s;
}
.abc-analysis .abc-header .header-right .export-btn:hover {
  background-color: #2563eb;
}
.abc-analysis .abc-header .header-right .export-btn svg {
  color: white;
}
.abc-analysis .abc-summary-cards {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 1.5rem;
  margin-bottom: 2rem;
}
.abc-analysis .abc-summary-cards .abc-card {
  background-color: white;
  border-radius: 0.75rem;
  border: 1px solid #e5e7eb;
  padding: 1.5rem;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
}
.abc-analysis .abc-summary-cards .abc-card .card-header {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  margin-bottom: 1rem;
}
.abc-analysis .abc-summary-cards .abc-card .card-header .class-badge {
  width: 2rem;
  height: 2rem;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  font-size: 1rem;
  color: white;
}
.abc-analysis .abc-summary-cards .abc-card .card-header .class-a-badge {
  background-color: #10b981;
}
.abc-analysis .abc-summary-cards .abc-card .card-header .class-b-badge {
  background-color: #f59e0b;
}
.abc-analysis .abc-summary-cards .abc-card .card-header .class-c-badge {
  background-color: #3b82f6;
}
.abc-analysis .abc-summary-cards .abc-card .card-header .card-title {
  font-size: 1.125rem;
  font-weight: 600;
  color: #374151;
  margin: 0;
}
.abc-analysis .abc-summary-cards .abc-card .card-metrics {
  margin-bottom: 1rem;
}
.abc-analysis .abc-summary-cards .abc-card .card-metrics .metric-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 0.5rem;
}
.abc-analysis .abc-summary-cards .abc-card .card-metrics .metric-row:last-child {
  margin-bottom: 0;
}
.abc-analysis .abc-summary-cards .abc-card .card-metrics .metric-row .metric-label {
  font-size: 0.875rem;
  color: #6b7280;
  font-weight: 500;
}
.abc-analysis .abc-summary-cards .abc-card .card-metrics .metric-row .metric-value {
  font-size: 0.875rem;
  color: #111827;
  font-weight: 600;
}
.abc-analysis .abc-summary-cards .abc-card .progress-bar {
  width: 100%;
  height: 0.5rem;
  background-color: #f3f4f6;
  border-radius: 0.25rem;
  overflow: hidden;
}
.abc-analysis .abc-summary-cards .abc-card .progress-bar .progress-fill {
  height: 100%;
  border-radius: 0.25rem;
  transition: width 0.3s ease;
}
.abc-analysis .abc-summary-cards .abc-card .progress-bar .progress-fill.class-a-fill {
  background-color: #10b981;
}
.abc-analysis .abc-summary-cards .abc-card .progress-bar .progress-fill.class-b-fill {
  background-color: #f59e0b;
}
.abc-analysis .abc-summary-cards .abc-card .progress-bar .progress-fill.class-c-fill {
  background-color: #3b82f6;
}
.abc-analysis .detailed-analysis {
  background-color: white;
  border-radius: 0.75rem;
  border: 1px solid #e5e7eb;
  padding: 1.5rem;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
}
.abc-analysis .detailed-analysis .section-title {
  font-size: 1.25rem;
  font-weight: 600;
  color: #111827;
  margin: 0 0 1.5rem 0;
}
.abc-analysis .detailed-analysis .controls-row {
  display: flex;
  gap: 1rem;
  margin-bottom: 1.5rem;
  align-items: center;
}
.abc-analysis .detailed-analysis .controls-row .search-container {
  position: relative;
  flex: 1;
  max-width: 400px;
}
.abc-analysis .detailed-analysis .controls-row .search-container .search-icon {
  position: absolute;
  left: 1rem;
  top: 50%;
  transform: translateY(-50%);
  width: 1.25rem;
  height: 1.25rem;
  color: #9ca3af;
}
.abc-analysis .detailed-analysis .controls-row .search-container .search-input {
  width: 100%;
  padding: 0.75rem 1rem 0.75rem 2.75rem;
  border: 1px solid #d1d5db;
  border-radius: 0.5rem;
  font-size: 0.875rem;
  background-color: #f9fafb;
}
.abc-analysis .detailed-analysis .controls-row .search-container .search-input:focus {
  outline: none;
  border-color: #3b82f6;
  background-color: white;
}
.abc-analysis .detailed-analysis .controls-row .search-container .search-input::placeholder {
  color: #9ca3af;
}
.abc-analysis .detailed-analysis .controls-row .filter-container {
  position: relative;
  min-width: 150px;
}
.abc-analysis .detailed-analysis .controls-row .filter-container .filter-select {
  width: 100%;
  padding: 0.75rem 2.5rem 0.75rem 1rem;
  border: 1px solid #d1d5db;
  border-radius: 0.5rem;
  font-size: 0.875rem;
  background-color: white;
  appearance: none;
  cursor: pointer;
}
.abc-analysis .detailed-analysis .controls-row .filter-container .filter-select:focus {
  outline: none;
  border-color: #3b82f6;
}
.abc-analysis .detailed-analysis .controls-row .filter-container .dropdown-arrow {
  position: absolute;
  right: 0.75rem;
  top: 50%;
  transform: translateY(-50%);
  width: 1rem;
  height: 1rem;
  color: #6b7280;
  pointer-events: none;
}
.abc-analysis .detailed-analysis .analysis-table-container {
  overflow-x: auto;
}
.abc-analysis .detailed-analysis .analysis-table-container .analysis-table {
  width: 100%;
  border-collapse: collapse;
}
.abc-analysis .detailed-analysis .analysis-table-container .analysis-table thead {
  background-color: #f9fafb;
  border-bottom: 1px solid #e5e7eb;
}
.abc-analysis .detailed-analysis .analysis-table-container .analysis-table thead th {
  padding: 1rem;
  text-align: left;
  font-size: 0.875rem;
  font-weight: 600;
  color: #374151;
  border-bottom: 1px solid #e5e7eb;
  white-space: nowrap;
}
.abc-analysis .detailed-analysis .analysis-table-container .analysis-table tbody tr {
  border-bottom: 1px solid #f3f4f6;
}
.abc-analysis .detailed-analysis .analysis-table-container .analysis-table tbody tr:last-child {
  border-bottom: none;
}
.abc-analysis .detailed-analysis .analysis-table-container .analysis-table tbody tr:hover {
  background-color: #f9fafb;
}
.abc-analysis .detailed-analysis .analysis-table-container .analysis-table tbody tr td {
  padding: 1rem;
  font-size: 0.875rem;
  color: #111827;
  vertical-align: top;
}
.abc-analysis .detailed-analysis .analysis-table-container .analysis-table tbody tr td.part-number {
  font-weight: 600;
  color: #3b82f6;
}
.abc-analysis .detailed-analysis .analysis-table-container .analysis-table tbody tr td.part-name {
  font-weight: 500;
}
.abc-analysis .detailed-analysis .analysis-table-container .analysis-table tbody tr td.category {
  color: #6b7280;
}
.abc-analysis .detailed-analysis .analysis-table-container .analysis-table tbody tr td.total-value {
  font-weight: 600;
  color: #10b981;
}
.abc-analysis .detailed-analysis .analysis-table-container .analysis-table tbody tr td.value-percentage, .abc-analysis .detailed-analysis .analysis-table-container .analysis-table tbody tr td.cumulative-percentage {
  font-weight: 500;
  text-align: center;
}
.abc-analysis .detailed-analysis .analysis-table-container .analysis-table tbody tr td.abc-class .abc-badge {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 0.25rem 0.75rem;
  border-radius: 9999px;
  font-size: 0.75rem;
  font-weight: 600;
  text-transform: uppercase;
  min-width: 2rem;
}
.abc-analysis .no-results {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 3rem 2rem;
  text-align: center;
  background-color: white;
  border-radius: 0.75rem;
  border: 1px solid #e5e7eb;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
}
.abc-analysis .no-results .no-results-title {
  color: #6b7280;
  font-size: 1.25rem;
  font-weight: 600;
  margin: 1rem 0 0.5rem 0;
}
.abc-analysis .no-results .no-results-description {
  color: #9ca3af;
  font-size: 1rem;
  margin: 0;
}
.abc-analysis .voice-assistant {
  position: fixed;
  bottom: 2rem;
  right: 2rem;
  width: 3.5rem;
  height: 3.5rem;
  background-color: #3b82f6;
  color: white;
  border: none;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
  transition: all 0.2s;
}
.abc-analysis .voice-assistant:hover {
  background-color: #2563eb;
  transform: translateY(-2px);
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
}
.abc-analysis .voice-assistant svg {
  color: white;
}

@media (max-width: 1024px) {
  .abc-analysis .abc-summary-cards {
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  }
  .abc-analysis .detailed-analysis .controls-row {
    flex-direction: column;
    align-items: stretch;
  }
  .abc-analysis .detailed-analysis .controls-row .search-container {
    max-width: none;
  }
  .abc-analysis .detailed-analysis .controls-row .filter-container {
    min-width: auto;
  }
}
@media (max-width: 768px) {
  .abc-analysis {
    padding: 1rem;
  }
  .abc-analysis .abc-header {
    flex-direction: column;
    gap: 1rem;
    align-items: stretch;
  }
  .abc-analysis .abc-header .header-right .export-btn {
    width: 100%;
    justify-content: center;
  }
  .abc-analysis .abc-summary-cards {
    grid-template-columns: 1fr;
    gap: 1rem;
  }
  .abc-analysis .abc-summary-cards .abc-card {
    padding: 1rem;
  }
  .abc-analysis .detailed-analysis {
    padding: 1rem;
  }
  .abc-analysis .detailed-analysis .analysis-table-container .analysis-table {
    min-width: 600px;
  }
}
@media (max-width: 480px) {
  .abc-analysis .abc-header .header-left .main-title {
    font-size: 1.5rem;
  }
  .abc-analysis .detailed-analysis .controls-row .search-container .search-input {
    padding: 0.75rem 1rem 0.75rem 2.5rem;
  }
}
